
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;  
}
body {
  font-family: 'Lato', sans-serif;
  color: #4c4c4c;
  background: #89609E;
}

.soo{
  display: hidden;
}
header {
 text-align: center;
 background: #745286;
 border-radius: .7em;
 margin: 30px;
 /*box-shadow: 5px 5px 5px grey;*/
 padding: 5px;
}

.comment{
  display:inline-block;
 border: 1px solid rgba(222, 225, 227, 0.9);
 border-radius: 10px;
 background: rgba(222, 225, 227, 0.9);
 margin: 10px;
 padding: 7px;
}

.userName{
 color: black;
 font-size: 1.3em;
}

h1,
p,
form button {
  color: white;  
}
h1 {
  font-family: 'Courgette', cursive;
  text-shadow: 0 1px 0 rgba(0,0,0, .4);
  line-height: .65;
  margin-top: .5em;
  margin-bottom: 0;
}
h2 {
  margin-top: 0;
}

form {
 margin: 0 auto;
 width: 80%;
  display: inline-block;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  border-radius: .2em;
}
form button {
  padding: 0 1em;
  font-size: 1em;
  background: #FDE880;
  border-radius: .2em;
}
ul {
  list-style: none;
  padding: 0;
  margin: 2em 0 1em;
}
ul li {
  padding: 1em;
  background: #fff;
  border-radius: .2em;
  border: solid 1px rgba(88, 183, 205, .2);
  border-bottom: solid 2px rgba(88, 183, 205, .2);
  position: relative;
}
button {
  cursor: pointer;
}
input, 
button {
  border: none;
  outline: none;
}

header input {
  padding: 12px;
  font-size: 1.15em;
  width: 100%;
}
li span, 
li input[type=text] {
  color: #707070;
  font-size: 1.3em;
  margin-bottom: .3em;
}
li input[type=text] {
  padding: .2em;
  width: 95%;
  border: 1px dotted rgba(0,0,0, .2);
}
li label {
  font-size: .9em;
  display: block;
  color: rgba(112, 112, 112, .55);
}
li span, 
li input[type=text]:first-child {
  display: inline-block;
}
li button {
  font-size: .78em;
  margin-top: 1.65em;
  margin-right: .4em;
  border-radius: .3em;
  padding: .4em .6em;
  color: white;
  background: #58b7cd;
}
li button:last-child {
  background: rgba(88, 183, 205, .5);
}

.wrapper {
  width: 100%;
  max-width: 900px;
  margin: 2.5em auto;
  border-radius: .35em;
  /*background: #1976D2;*/
  overflow: hidden;
 /* box-shadow: 0 0 26px rgba(0,0,0, .13);*/
}
div > input:last-child {
  font-size: 1em;
  margin-left: 6px;
}
div > label {
  color: #767676;
}

/* responded */
.responded {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  border-color: rgba(88, 183, 205, .9);
}
.responded label {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  color: rgba(88, 183, 205, 1);
}

/*Navbar*/
#k-nav{
  background: #745286;
}

#k-nav .navi{
  text-align: center;
  margin: 0;
  padding: 0.4em;
}



/*****************************/


.oauth-buttons {
  text-align:center;
}
.row {
text-align:center;
}
#login-form{
min-width:375px;   
}
.login-container{
width:400px;
height:330px;
display:inline-block;
margin-top: -165px;
  top: 50%;
  left: 50%;
  position: absolute;
  margin-left: -200px;
}
.form-container .create-account:hover{
  color: white;
}
.form-container .create-account{
  color:inherit;
  margin-top: 15px;
  display: inline-block;
  cursor:pointer;
  text-decoration:none;
}

.oauth-buttons .fa{
cursor:pointer;
margin-top:10px;
color:inherit;
width:30px;
height:30px;
text-align:center;
line-height:30px;
margin:5px;
margin-top:15px;
}
.oauth-buttons .fa:hover{
color:white;
}
.oauth-buttons .fa-google-plus:hover{
background: #dd4b39;
}
.oauth-buttons .fa-facebook:hover{
background:	#8b9dc3;
}

.oauth-buttons .fa-linkedin:hover{
background:	#0077b5;
}

.oauth-buttons .fa-twitter:hover{
background:	#55acee;
}

.form-container .req-input .input-status {
  display: inline-block;
  height: 40px;
  width: 40px;
  float: left;	
}
.form-container .input-status::before{
content: " ";
height:20px;
width:20px;
position:absolute;
top:10px;
left:10px;
color:white;
border-radius:50%;
background:white;
-webkit-transition: all .3s ease-in-out;
-o-transition: all .3s ease-in-out;
transition: all .3s ease-in-out;

}

.form-container .input-status::after{
content: " ";
height:10px;
width:10px;
position:absolute;
top:15px;
left:15px;
color:white;
border-radius:50%;
background:#00BCD4;
-webkit-transition: all .3s ease-in-out;
-o-transition: all .3s ease-in-out;
transition: all .3s ease-in-out;
}

.form-container .req-input{
width:100%;
  float:left;
position:relative;
background:#00BCD4;
height:40px;
display:inline-block;
border-radius:0px;
margin:5px 0px;
-webkit-transition: all .3s ease-in-out;
-o-transition: all .3s ease-in-out;
transition: all .3s ease-in-out;
}

.form-container div .row  .valid:hover{
background:#A5D6A7;
}

.form-container div .row .valid {
background:#81C784;

}

.form-container .valid .input-status:after {
border-radius:0px;
  width: 17px;
  height: 4px;
  background: white;
  top: 16px;
  left: 15px;
  -ms-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.form-container .valid .input-status:before {
border-radius:0px;
  width: 11px;
  height: 4px;
background:white;
  top: 19px;
  left: 10px;
  -ms-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.form-container .input-container{
padding:0px 20px;
}

.form-container .row-input{
padding:0px 5px;
}

.form-container {
margin:20px auto;
padding:20px;
border-radius:0px;
}

.form-container .form-title{
font-size:25px;
color:inherit;
text-align:center;
margin-bottom:10px;

}

.form-container .submit-row{
padding:0px 0px;
}

.form-container .btn.submit-form{
margin-top:15px;
padding:12px;
background: #89609E;
color:white;
border: 1px solid white;
text-decoration: none;
display: inline-block;
}

.form-container .btn.submit-form:hover{
  margin-top:15px;
  padding:12px;
  background: white;
  color: #89609E;
  border: 1px solid white;
  text-decoration: none;
  }



.form-container .tooltip.top .tooltip-inner::before{
content:" ";
width:100%;
height:6px;
background:#00BCD4;
position:absolute;
bottom:5px;
right:0px;
}
.form-container .tooltip.top .tooltip-inner{
border:0px solid #00BCD4;
background:#E0F7FA !important;
color:#00ACC1;
font-weight:bold;
font-size:13px;
border-radius:0px;
padding:10px 15px;
}
.form-container .tooltip {
max-width:150px;
opacity:1 !important;
}

.form-container .message-box{
width:100%;
height:auto;
}

.form-container textarea:focus,.form-container textarea:hover{
background:#fff;
outline:none;
border:0px;
}

.form-container .req-input textarea {
max-width:calc(100% - 50px);
  width: 100%;
  height: 80px;
  border: 0px;
  color: #777;
  padding: 10px 9px 0px 9px;
float:left;

}
.form-container input[type=text]:focus, .form-container input[type=password]:focus, .form-container input[type=email]:focus, .form-container input[type=tel]:focus, .form-container select{
  background:#fff;
color:#777;
border-left:0px;
outline:none;
}

.form-container input[type=text]:hover,.form-container input[type=password]:hover,.form-container input[type=email]:hover,.form-container input[type=tel]:hover, . form-container select{
background:#fff;
}

.form-container input[type=text], .form-container input[type=password], .form-container input[type=email],input[type=tel], form-container select{
width:calc(100% - 50px);
float:left;
border-radius:0px;
border:0px solid #ddd;
padding:0px 9px;
height:40px;
line-height:40px;
color:#777;
background:#fff;
-webkit-transition: all .3s ease-in-out;
-o-transition: all .3s ease-in-out;
transition: all .3s ease-in-out;
}


